@import "src/styles/statement";

#introduction {
  width: 100%;
  height: 100%;

  .header {
    position: relative;
    top: 200px;
    left: 550px;

    h1 {
      font-size: 40px;
    }


  }

  //所有移动设备
  @include styleMobile {
    .header {
      top: conversionVH(150);
      left: conversionPX(25);

      h1 {
        font-size: conversionPX(25);
      }
      .svg {
        width: conversionPX(320);
      }
    }

  }
}