@import "./src/styles/statement";

#navigation {
  position: fixed;
  top: 300px;
  left: 100px;
  z-index: 10;

  /*列表*/
  .list {
    width: 200px;
    border: 2px gray solid;
    border-radius: 10px;
    &:hover {
      .text {
        opacity: 1;
      }
    }

    li {
      display: flex;
      align-items: center;
      cursor: pointer;
      transition-duration: 0.5s;
      &:hover {
        background-color: gray;
      }
    }

    span {
      font-size: 35px;
      margin-left: 10px;
      color: #E6E1D9;
    }

    .text {
      opacity: 0;
      font-size: 25px;
      margin-left: 20px;
      color: #E6E1D9;
      transition-duration: 0.5s;
      user-select: none;
    }
    //被选中的时候
    .choose {
      opacity: 1;
      color: #EBEBE4;
    }
  }

  /*按钮*/
  .button {
    position: fixed;
    font-size: 50px;
    left: 49vw;
    color: #E6E1D9;
    cursor: pointer;
  }

}

@include styleMobile {
  #navigation {
    display: none;
  }
}
