//手机屏幕
@mixin styleAllPhone {
  @media screen and (max-width: 767px) {
    @content;
  }
}

//所有移动设备
@mixin styleMobile {
  @media screen and (max-width: 1024px) {
    @content;
  }
}
//加长设备：如iPhonex
@mixin styleLongHeight{
  @media screen and (min-height: 810px) and (max-height: 823px){
    @content;
  }
}
//小平板屏幕
@mixin styleMinPad {
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    @content;
  }
}

//除了大平板以外
@mixin styleOtherMPad {
  @media screen and (max-width: 769px) {
    @content;
  }
}

//平板屏幕
@mixin stylePad {
  @media screen and (min-width: 768px) and (max-width: 1024px){
    @content;
  }
}

//以iPhone6的屏幕宽度为准转换成rem
@function conversionPX($px) {
  @return $px/100 + rem;
}

//以1920的屏幕宽度为准转换成rem
@function conversionPCPX($px) {
  @return $px/512 + rem;
}

//以1920的屏幕宽度为准转换成vw
@function conversionPCVW($px) {
  @return 100/1920*$px + vw;
}

//以1080的屏幕高度为准转换成vh
@function conversionPCVH($px) {
  @return 100/1080*$px + vh;
}

//以iPhone6的屏幕高度为准转换成vh
@function conversionVH($px) {
  @return 100/667*$px + vh;
}

//垂直居中
@mixin verticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}

//垂直水平居中
@mixin allCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


