@import "src/styles/statement";

#show {
  position: relative;
  .htmlGame,
  .game,
  .miniPorgram {
    @include allCenter;
    position: absolute;
    width: 700px;
    height: 400px;
    border: 1px black solid;
    border-radius: 10px;
    background-color: rgba(128, 128, 128, 0.5);

    h2 {
      margin-top: 5px;
    }
  }

  .miniPorgram {
    .image {

      img {
        height: 300px;

        &:nth-child(2) {
          margin-left: 50px;
        }
      }
    }

    top: 100px;
    left: 1100px;
  }

  .game {
    left: 680px;
    top: 550px;

  }

  .htmlGame {
    top: 100px;
    left: 370px;
    h2>a {
      margin: 0 40px;
      padding: 0 10px;
      color: black;
      border: 1px solid black;
    }
  }

  @include styleMobile {
    .htmlGame,
    .game,
    .miniPorgram {
      position: absolute;
      width: conversionPX(320);
      height: conversionVH(200);
      left: conversionPX(25);

      h2 {
        font-size: conversionVH(20);
        margin: conversionPX(2) 0 0 10px;
      }
    }

    .miniPorgram {
      .image {

        img {
          height: conversionVH(130);
        }
      }

      top: conversionVH(232);
    }

    .game {
      top: conversionVH(440);

      img {
        height: conversionVH(140);
      }
    }

    .htmlGame {
      top: conversionVH(25);

      img {
        height: conversionVH(95);
      }

      h2>a {
        margin: 0 conversionPX(5);
        font-size: 16px;
      }
    }
  }

  @include stylePad {
    .htmlGame,
    .game,
    .miniPorgram {
      h2 {
        font-size: 20px;
        margin-left: 10px;
      }
    }

    .miniPorgram {
      .image {

        img {
          height: conversionVH(120);
        }
      }
    }

    .game {
      img {
        width: conversionPX(150);
      }
    }

    .htmlGame {
      img {
        width: conversionPX(150);
      }
    }
  }
  @include styleLongHeight {
    .htmlGame,
    .game,
    .miniPorgram {

      h2 {
        font-size: 20px;
      }
    }
  }
}
